.vim-tooltip {
  margin-left: -20px;
  z-index: 999;
  position: absolute;
  display: block;
  font-size: 8pt;
  font-weight: bold;
  color: #000000;
  -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0px 3px 7px 0px;
          box-shadow: rgba(0, 0, 0, 0.3) 0px 3px 7px 0px;
  width: auto;
  padding: 0px 2px;
  background: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#fff785), to(#ffc542));
  border-width: 1px;
  border-style: solid;
  border-color: #c38a22;
  -webkit-border-image: initial;
       -o-border-image: initial;
          border-image: initial;
  border-radius: 3px;
}



